import React from 'react'
import { useStaticQuery, graphql, Link as GLink } from 'gatsby'
import { GatsbyImage as Img, getImage } from 'gatsby-plugin-image'
import { Box, Text, Card, Flex, Button } from 'theme-ui'
import { Layout, Stack, Main } from '@layout'
import PageTitle from '@components/PageTitle'
import Divider from '@components/Divider'
import Section from '@components/Section'
import Seo from '@widgets/Seo'

/**
 * Shadow me to add your own content
 */

const styles = {
  imageWrapper: {
    borderRadius: `default`,
    overflow: `hidden`,
    position: `relative`
  },
  button: {
    display: [`none`, `block`],
    position: `absolute`,
    bottom: 4,
    right: 4
  },
  grid: {
    flexWrap: [`wrap`, null, `nowrap`],
    ' > div + div': {
      ml: [0, 0, 5]
    }
  },
  column: {
    flex: `auto`,
    flexBasis: [`full`, null, `1/2`]
  }
}

export default props => {
  const data = useStaticQuery(aboutQuery)
  const image = getImage(data.avatar)

  return (
    <Layout {...props}>
      <Seo title='About' />
      <Divider />
      <Stack>
        <Main>
          <PageTitle
            header='Sobre Raphael Barros'
            subheader='O futuro pertence àqueles que acreditam na beleza dos seus sonhos.'
          />
          
          <Flex sx={styles.grid}>
            <Box sx={styles.column}>
              <Section title='Minha História'>
                <Text variant='p'>
                  Olá Meu nome é Raphael Barros, e minha maior missão na terra poder iluminar a vida de milhares de pessoas. Demonstrar como a vida pode ser bela e incrivél idependente da situação
                  que a gente se encontre. Acredito que a felicidade é simples e os eventos da vida são neutros, apenas nossa mente tem a capacidade de dar sentido de bom ou ruim aos eventos que se dedobram a nossa frente.
                </Text>
                <Text variant='p'>
                  Criei este site como uma forma de eternizar meus pensamentos, meu desenvolvimento aprendizados, erros, e espero que de alguma forma possa ajudar outras pessoas a ver a verdade da vida. A verdade por trás das palavras, ações das pessoas, 
                  aquilo que não vemos e tememos, a verdade por trás do fluxo da vida e da sensação de falta de controle que temos sobre ela. A verdade que há por trás da dor... Talvez este blog seja mais para min e meus filhos(as) do que necessáriamente para o mundo. 
                  Uma vez que meu tempo tenha esgotado, que meus aprendizados e erros possam auxiliar a caminhada daqueles que amo. Que minhas palavras possam trazer luz , clareza, motivação nos momentos dificeis. 
                </Text>
                <Text variant='p'>
                  Para que assim, pouco a pouco a gente possa ter uma sociedade mais livre, consciênte de suas emoções, mais empatica, amorosa e capaz de respeitar o próximo e suas diferenças e limitações. Entendendo que cada um possui uma jornada, um tempo de aprendizado e evolução. 
                  Para que possamos entender que o amor é a ferramenta mais poderosa que há, capaz de restaurar, curar reestabelecer e fortalecer nossas vidas e almas. 
                </Text>
                <Text variant='p'>
                  Acredito que a pergunta que fica, mas quem é Raphael Barros. É algo que sempre me pergunto... Contudo a definição é algo muito complexo, uma vez que somos uma imensidão de coisas. Não sou um guru, mas me identifico com desenvolvimento pessoal, emocional e espiritual.
                  Não sou um desenvolvedor de software, mas consigo desenvolver qualquer programa e projeto que desejar. Não ou um Gestor de tráfego orgânico, mas posso dizer que sou um especialista em SEO e tráfego orgânico. O que quero dizer é que no momento em que nos definimos como algo, nos limitamos, 
                  ficamos enquadrados e limitados a nossa percepção de nós mesmos. 
                </Text>
                <Text variant='p'>
                  Ou seja, se acredito ser apenas um especialista SEO, vou agir, falar, pensar e me comportar exclusivamente apenas como isso. Contudo todos nós possuimos um potêncial infinito quando não nos limitamos a titulos ou definições. E é desta forma que me enxergo, como infinitas possibilidades, onde 
                  posso ser excepcional em cada uma das quais eu me comprometer. Pois minha maior virtude e qualidade está em buscar ser o melhor seja no que for, em nunca desistir do que acredito. Sim por pior que possa ser a situação eu nunca vou desistir do que me comprometi! 
                </Text>
                              
              </Section>
            </Box>
            <Box sx={styles.column}>
              <Section title='Art Directing'>
                <Card variant='SEO'>
                  During the brainstorming process, art directors, co-workers,
                  and clients are engaged in imagining what the finished piece
                  or scene might look like.
                </Card>
              </Section>
              <Divider />
              <Section title='Digital Marketing'>
                <Card variant='Bitcoin & Crypto'>
                  Digital marketing channels are systems based on the Internet
                  that can create, accelerate, and transmit product value from
                  producer to a consumer terminal, through digital networks
                </Card>
              </Section>
              <Divider />
              <Section title='Engenheiro de Software'>
                <Card variant='paper'>
                  A core responsibility of the designer's job is to present
                  information in a way that is both accessible and memorable.
                </Card>
              </Section>
            </Box>
          </Flex>
        </Main>
      </Stack>
    </Layout>
  )
}

const aboutQuery = graphql`
  query AboutQuery {
    avatar: file(absolutePath: { regex: "/about.(jpeg|jpg|gif|png)/" }) {
      childImageSharp {
        gatsbyImageData(
          width: 1140
          height: 500
          transformOptions: { cropFocus: NORTH }
        )
      }
    }
  }
`
